<script>
import axios from "axios";
import searchBar from '../searchBar.vue'
import router from '../../common/router.js';
import EncyclopediaResultDetail from './/resultPages/EncyclopediaResultDetail.vue'

export default {
  components: {
    searchBar,
    EncyclopediaResultDetail
  },
  props: ['homeCountry'],
  data() {
    return {
      books: [],
      articles: [],
      movies: [],
      moviesAll: [],
      podcasts: [],
      description: "",
      title: "",
      countryChoice: "",
      originalCountry: "",
      countryModal: true
    };
  },
  mounted() {
    if(this.$route.query.homeCountry){
      this.countryChoice = this.$route.query.homeCountry;
    }else{
      this.countryChoice = sessionStorage.getItem("storedCountry");
    }

    if(this.countryChoice != null){
      this.searchCountry(this.countryChoice);
    }
  },
  methods:{
    async searchCountry(country){
      router.push({ path: 'search', query: { homeCountry: country }});
      this.movies = [];
      this.countryChoice = country.toLowerCase();
      this.originalCountry = country;
      
      sessionStorage.setItem("storedCountry", this.countryChoice);

      const bookResponse = await axios.get("/api/books/country/" + this.countryChoice);
      
      const articleResponse = await axios.get("/api/articles/country/" + this.countryChoice);

      const movieResponse = await axios.get("/api/movies/country/" + this.countryChoice);

      const podcastResponse = await axios.get("/api/podcasts/country/" + this.countryChoice);

      this.books = bookResponse.data;
      this.articles = articleResponse.data;
      this.moviesAll = movieResponse.data;
      //this.movies = movieResponse.data;
      this.podcasts = podcastResponse.data;

      this.filterMovies();

      this.countryModal = false;
    },
    filterMovies(){

      for(let m = 0; m < this.moviesAll.length; m++){

        if(this.moviesAll[m].genre_ids.length > 0){

          for(let g = 0; g < this.moviesAll[m].genre_ids.length; g++){
              
            if(this.moviesAll[m].genre_ids[g] == 99 || this.moviesAll[m].genre_ids[g] == 36){

              this.movies.push(this.moviesAll[m]);
              break;
            }
          }
        }
      }
    }


  }

}

</script>

<template>
  
  <searchBar v-bind:countryModal="countryModal" v-on:search-country='searchCountry($event)'/>
  <h1>{{homeCountry}}</h1>
  <div class = "search-results">
      <mediaTabs>
          <div class="tab-anchor" id="overview"></div>
          <mediaTab name="Overview">
            <div class="results-list">
              <EncyclopediaResultDetail v-bind:country="originalCountry"/>
            </div>
            
          </mediaTab>
              <div class="tab-anchor" id="books"></div>
          <mediaTab name="Books">
            <h4>Books</h4>
            <div class="results-list">
              <p v-if="books.length == 0">No Results</p>
              <router-link
              v-for="book in books"
              :key="book.id"
              :to="{
               name: 'bookMedia',
               params: { id: book.id},
              }" 
                >
            <span class="list-row">{{ book.volumeInfo.title }}</span>
              </router-link>
            </div>
            <small>*From Google Books</small>
          </mediaTab>
              <div class="tab-anchor" id="articles"></div>
          <mediaTab name="Articles">
            <h4>Articles</h4>
            <div class="results-list">
              <p v-if="articles.length == 0">No Results</p>
              <router-link
              v-for="article in articles"
              :key="article.id"
              :to="{
               name: 'articleMedia',
               params: { id: article.id},
              }" 
                >
            <span class="list-row">{{ article.headline.main }}</span>
              </router-link>
            </div>
              <small>*From NY Times</small>
          </mediaTab>
              <div class="tab-anchor" id="podcasts"></div>
          <mediaTab name="Podcasts">
              <h4>Podcasts</h4>
            <div class="results-list">
              <p v-if="podcasts.length == 0">No Results</p>
              <router-link
              v-for="podcast in podcasts"
              :key="podcast.id"
              :to="{
               name: 'podcastMedia',
               params: { id: podcast.id},
              }" 
                >
            <span class="list-row">{{ podcast.title_original }}</span>
              </router-link>
            </div>
              <small>*From Listen Notes</small>
          </mediaTab>
              <div class="tab-anchor" id="documentaries"></div>
          <mediaTab name="Documentaries">
            <h4>Documentaries</h4>
            <div class="results-list">
              <p v-if="movies.length == 0">No Results</p>
              <router-link
              v-for="movie in movies"
              :key="movie.id"
              :to="{
               name: 'movieMedia',
               params: { id: movie.id},
              }" 
                >
            <span class="list-row">{{ movie.title }}</span>
              </router-link>
            </div>
            <small>*From The Movie Database</small>
          </mediaTab>

      </mediaTabs>
  </div>
</template>

