<script>
import axios from "axios";
export default {
  props: ['country'],
  data() {
    return {
      content: {},
      id: String,
      url: String,
      overview: String,
      title: String
    }
  },
  async mounted() {
      const response = await axios.get("/api/encyclopedias/" + this.country);
      this.content = await response.data;
      this.title = await this.content[0].country;
      this.id = await this.content[0].id;
      this.url = await this.content[0].url;
      this.overview = await this.content[0].overview;

      this.updateOverview();
    
  },
  watch: {
    country: async function () {
      const response = await axios.get("/api/encyclopedias/" + this.country);
      this.content = await response.data;
      this.title = await this.content[0].country;
      this.id = await this.content[0].id;
      this.url = await this.content[0].url;
      this.overview = await this.content[0].overview;


      this.updateOverview();
    }
  },
  methods: {
    updateOverview(){
      this.overview = this.overview.replace(/(<)(.*?)(>)/g,"");
      this.overview = this.overview.replace(/(^\s\S*)/g,"");
      this.overview = this.overview.replace(/(officially)/g,"officially ");
    }
  }
}

</script>

<template>
  <p v-if="title == null" class="default-message">Please Choose a Country</p>
  <div v-if="typeof title != 'function'">
    <h2>{{ title }}</h2>
    <h3 v-if="title != null">Overview</h3>
    <p>{{ overview }}</p>

  </div>
</template>


<style>
@import '../../../assets/css/style.css';
